    font-style: italic;
}
.myTableHeader{
    display: flex;
    border-bottom: 1px solid gainsboro;
    margin-left: 15px;
    margin-right: 15px;
    height: 50px;
    padding: 5px;
}
.myTable{
    margin-left: 15px;
    margin-right: 15px;
    display: flex;
    color: #8B8B8B;
    height:60px;
}


.myTable:hover{
    box-shadow: 0 0 16px 3px #cccccc;
    opacity: 80%;
    background-color:#334452 ;
}
.newClassName{
    background-color: white;
}
.myTable div{
    padding: 5px;
    border-bottom: 1px solid gainsboro;
    background-color: white;
}
